@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;1,600;1,700&display=swap');

:root{
  --frist-color: #19747E;
  --title-color: #A9D6E5;
  --text-color: #D1E8E2;
  --body-color: #E2E2E2;
  --container-color: #fff;
  --body-font: 'Poppins', sans-serif;

  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;

  --font-medium: 500;
  --font-semibold: 500;
  --font-bold: 500;

  --shadow: 0px 5px 20px 0px rgb(69 67 96 / 10% );

  --border-radius: 20px;
}

@media screen and (max_width: 1024px) {
  :root{
    --h1-font-size: 1.75rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1rem;
    --normal-font-size: 0.938rem;
    --small-font-size: 0.813rem;
    --smaller-font-size: 0.75rem;
  }
}

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}
body, button, input, textarea{
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}
body{
  background-color: var(--frist-color);
  line-height: 1.7;
  color: var(--text-color);
}
h1, h2, h3{
  color: var(--title-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;
}
ul{
  list-style: none;
}
a{
  text-decoration: none;
}
button{
  cursor: pointer;
  border: none;
  outline: auto;
}
img{
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}
.container{
  max-width: 1080px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}
.grid{
  display: grid;
}
.section{
  padding-top: 7rem;
  padding-bottom: 2rem;
}
.section__title{
  font-size: var(--h1-font-size);
  margin-left: 0.875rem;
  font-weight: var(--font-bold);
  position: relative;
  margin-bottom: 3.75rem;
}
.section__title::before{
  content: '';
  background: url(./assets/dots-bg.svg);
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.875rem;
  top: -0.875rem;
}
.btn{
   padding: 0.75rem 2rem;
   line-height: 1;
   border-radius: 1.875rem;
   box-shadow: 0 0 1px rgb(0 0 0 / 0%); 
   border: 1px solid transparent;
   color: #fff;
   display: inline-block;
   background-color: var(--title-color);
   font-weight: var(--font-bold);
}
@keyframes button-push{
  50%{
    transform: scale(0.8);
  }
  100%{
    transform: scale(1);
  }
}
.btn:hover{
  animation: button-push 0.3s linear 1;
}